}

.checkStyle label {
  position: relative;
  cursor: pointer;
  margin-bottom: 10px;
  color: var(--darkgrey);
  padding-left: 26px;
}

.checkStyle label:before {
  content: "";
  border: 1px solid var(--darkgrey);
  cursor: pointer;
  width: 16px;
  height: 16px;
  position: absolute;
  left: 0;
  top: 2px;
}

.checkStyle input:checked + label:before {
  border-color: var(--primaryColor);
}

.checkStyle input:checked + label:after {
  content: "";
  display: block;
  position: absolute;
  top: 3px;
  left: 6px;
  width: 5px;
  height: 10px;
  border: solid var(--primaryColor);
