  mask: url(assets/images/grommet-maintenance.svg) no-repeat center;
}

.chargeIcon {
  -webkit-mask: url(assets/images/charge-maintenance.svg) no-repeat center;
  mask: url(assets/images/charge-maintenance.svg) no-repeat center;
}

.customListIcon {
  position: absolute;
  left: -16px;
  top: 3px;
  background-color: var(--navColor);
  width: 20px;
  height: 20px;
}

.customListIcon:hover {
  background-color: var(--primaryColor);
}

.iconActive {
  background-color: var(--primaryColor);
}

.subMenuLink:hover ~ .customListIcon {
  background-color: var(--primaryColor);
}

#auditLogsTable .dataTables_empty {
  display: none;
}
.theme2 .message_class {
  background-color: #1b1b29 !important;
  border-color: #a5a5be !important;
  color: #92929f !important;
}
